import axios from 'axios'

export default {
  state: {
    login: []
  },

  mutations: {
    setLogin (state, list) {
      state.login = list;
    }
  },

  actions: {
    signIn(context, credentials){
      axios.post("/login", {}, {
        withCredentials: true,
        auth: {
          username: credentials.login,
          password: credentials.password
        }
      }).then(response => {
        console.log(response);
      })
        .catch(error => {
          console.log(error);
        })
    }

  }

}




